    color: red;
}

.login-container .login-content {
    display: flex;
    flex-direction: column;
    width: 90%;
    max-width: 400px;
    padding: 30px;
    background: #FFF;
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.1);
    border-radius: 16px;
    align-items: center;

}
.login-container .login-form {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 400px;
    padding: 30px;
    border-radius: 16px;
    align-items: center;
}
.login-container .login-content input {
    margin-top: 4px;
}

.login-container section {
    width: 100%;
    max-width: 350px;
    justify-content: center;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    align-items: center;
}

.line {
    height: 1px;
    width: 80%;
    background-color: #707070;
}

.login-container section h1 {
    font-size: 32px;
    font-weight: 300;
    text-align: center;
}

.login-container section a {
    display: flex;
    align-items: center;
    margin-top: 10px;
    font-size: 18px;
    text-decoration: none;
    font-weight: 500;
    transition: opacity 0.2s;
}

.login-container section a:hover {
    opacity: 0.8;
}
.register-container {
    width: 100%;
